export default {
	data() {
		return {
			userInfo:{},
			user:{
				pwd:'',
				confirmPwd:'',
				newPwd:''
			}
		}
	},
	methods: {
		getUserInfo(){
			uni.getStorage({
				key:'userInfo',
				success: (res) => {
					this.userInfo=res.data
				}
			})
		}
	},
	onLoad(){
		this.getUserInfo()
	}
}
